
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapState,mapGetters,mapMutations } from 'vuex'
import VmBackTop from 'vue-multiple-back-top'
export default {
name: 'app',
data:function(){
return{
badgeText:'',










}
},
components: {
XHeader,
Actionsheet,
VmBackTop,
Badge
},
created:function(){
var loginStatus=window.localStorage.getItem('loginStatus')

if(loginStatus){
this.mutationIsLogined();
this.mutationLoginname(window.localStorage.getItem('loginname'))
this.mutationAccesstoken(window.localStorage.getItem('accesstoken'))
this.mutationAuthorId(window.localStorage.getItem('author_id'))
}

if(this.isLogined){

this.getOnreadMsgCount();

var app=this;
window.setInterval(function(){
app.getOnreadMsgCount();
},5*60*1000);
}

},
methods:{

getOnreadMsgCount:function(){
var app=this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){

if(res.data&&res.data.success){
app.badgeText=res.data.data
}
})
},
...mapMutations(['mutationIsLogined','mutationLoginname','mutationAccesstoken','mutationAuthorId']),

routetohome:function(){
this.$router.replace('/');
},

ShowMoreMenus:function(){
this.isShowMoreMenus=true;
},
clickMoreMenu:function(menukey,menuitem){
this.$router.push('/'+menukey)
}
},
computed:{







...mapState(['isShowMoreMenus','isLogined','unLoginedMoreMenus','loginedMoreMenus']),
...mapGetters(['moreMenus']),
isShowMoreMenus:{


get:function(){return this.$store.state.isShowMoreMenus},

set:function(nval){this.$store.commit('mutationIsShowMoreMenus',nval)},
},
},
}



