import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store=new Vuex.Store({
    state:{
        isShowMoreMenus:false,   //actionsheet菜单默认不显示
        isLogined:false,   //登陆状态
        loginname:'',   //登录名称
        accesstoken:'', //token码
        author_id:'',
        unLoginedMoreMenus:{ //未登录状态的菜单
          login:'登录'
        },
        loginedMoreMenus:{  //登陆状态的菜单
          create:'发表新话题',
          center:'个人中心',
          message:'消息通知',
          logout:'登出'
        },
        topics:[
          {name:'all',desc:'全部'},
          {name:'good',desc:'精华'},
          {name:'share',desc:'分享'},
          {name:'ask',desc:'问答'},
          {name:'job',desc:'招聘'},
          {name:'test',desc:'测试'}
        ]
      },
      getters:{
        moreMenus:function(state){
            //参数表示当前store对象的state
            if(state.isLogined){
              return state.loginedMoreMenus
            }
            return state.unLoginedMoreMenus
          },
          
          getTopicDescByTab:function(state){
            //需要返回一个函数结构
            return function(tab){
              for (var i=0;i<state.topics.length;i++){
                if(state.topics[i].name==tab){
                  return state.topics[i].desc
                }
              }
            }
          }
      },
      mutations:{


          //state数据必须放在mutation函数中统一修改
          mutationIsShowMoreMenus:function(state,payload){
              state.isShowMoreMenus=payload;
          },
          //修改登陆状态
        mutationIsLogined:function(state){
          state.isLogined=true
        }, 
        //修改登陆名
        mutationLoginname:function(state,payload){
          state.loginname=payload;
        },
        //修改accesstoken
        mutationAccesstoken:function(state,payload){
          state.accesstoken=payload
        },
        //用户注销
        mutationLogout:function(state){
          state.isLogined=false;
          state.loginname='';
          state.accesstoken=''
        },
        //修改author_id
        mutationAuthorId:function(state,payload){
          state.author_id=payload;
        }
      },
      actions:{}
})
export default store;


