import Vue from 'vue'
import Router from 'vue-router'
import TopicList from '../components/TopicList.vue';
import Login from '../components/Login.vue'
import Center from '../components/Center.vue'
import Message from '../components/Message.vue'
import Logout from '../components/Logout.vue'
import Topic from '@/components/Topic.vue'        //@表示src
import User from '@/components/User.vue'
import Create from '@/components/Create.vue'
import Edit from  '@/components/Edit.vue'


Vue.use(Router)

//以下代码解决路由地址重复点击的报错问题
const originalPush = Router.prototype.push
   Router.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default new Router({
  routes: [
    {path:'/',redirect:'/topiclist/all'},
    {path:'/topiclist/:name',component:TopicList,props:true},
    {path:'/topic/:id',component:Topic,props:true},
    {path:'/login',component:Login},
    {path:'/center',component:Center},
    {path:'/user/:loginname',name:'user',component:User,props:true},
    {path:'/message',component:Message},
    {path:'/logout',component:Logout},
    {path:'/create',name:'create',component:Create},
    {path:'/topic/:id/edit',name:'edit',component:Edit,props:true},

  ]
})



